.widget {
  text-align: center;
}

.widget p {
  margin-top: 0;
  margin-bottom: 16px;
  font-size: 48px;
}

.widget span {
  font-family: monospace;
}

.widget button {
  color: #4a6163;
  cursor: pointer;
  border: 2px solid #f9a66c;
  border-radius: 6px;
  padding: 8px 12px;
  font-size: 20px;
  font-weight: 600;
  transition: transform .2s;
}

.widget button:hover {
  transform: scale(1.1);
}

